@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap);
ul {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
  text-transform: capitalize;
}

header {
  background: #ffffff;
  padding: 10px 20px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  height: 40px;
}

.active .logo {
  display: none;
}

.menu-icon {
  font-size: 30px;
}

.nav-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  height: 90vh;
  top: 80px;
  left: -100%;
  transition: all 0.5s ease;
  width: 100%;
}

.nav-menu .logo {
  display: none;
}

.nav-menu li {
  margin: 0 0 60px;
}

header .active {
  align-items: center;
  left: 0;
  transition: all 0.6s ease;
  background-color: #ffffff;
  opacity: 1;
  margin-top: -15px;
}

.active li {
  margin: 0 0 60px;
}

.nav-menu a {
  font-size: 18px;
  font-weight: 600;
  color: #000;
}

.nav-menu a:hover {
  color: var(--primary-color);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: all 0.3s ease;
}

@media screen and (min-width: 540px) {
  header {
    padding: 20px 30px;
  }

  .nav-menu {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}

@media screen and (min-width: 768px) {
  header {
    padding: 10px 50px;
  }

  section.header {
    display: none;
  }

  header ul,
  header li {
    display: flex;
    align-items: center;
  }

  .nav-menu li {
    margin: 0 15px;
  }

  header .nav-menu {
    position: unset;
    flex-direction: row;
    height: initial;
    transition: all 0.5s ease;
    align-items: baseline;
    margin: 0;
  }

  .nav-menu .logo {
    display: block;
  }

  .nav-menu a:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  .nav-menu li:nth-child(2) > ul > li {
    margin: 0 20px;
  }
}

