.carousel {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 1px 1px 4px #e5e5e5;
  border-radius: 5px;
  z-index: 0;
  /* padding-bottom: 30px */
  /* z-index: 2; */
}

.carousel-indicators {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #f7f7f7;
  border-radius: 30px;
  padding: 10px;
  margin-bottom: -30px;
  align-items: center;
}

.carousel .active {
  background-color: #fff;
}

