  font-size: 15px;
  margin: 7px 0 15px;
}

footer p,
footer li {
  font-size: 14px;
  margin-top: 10px;
}

footer li {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
}

footer a {
  color: #000000;
}

footer a:hover {
  color: var(--primary-color);
}

footer hr {
  border: 1px solid rgba(237, 121, 113, 0.15);
}

li > div {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  width: 70%;
}

footer span {
  font-size: 16px;
  font-weight: 600;
}

@media screen and (min-width: 540px) {
  footer > ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0 20px;
  }

  footer p,
  footer li {
    font-size: 15px;
    margin-top: 7px;
  }

  footer img[alt="footer logo"] {
    margin-bottom: 10px;
  }

  li > div {
    width: 100%;
  }

  li.copywright {
    text-align: center;
    width: 300%;
  }
}

@media screen and (min-width: 768px) {
  footer {
    padding: 100px 70px;
  }

  footer > ul {
    grid-template-columns: repeat(5, 1fr);
  }

  footer li {
    align-items: left;
  }

  footer ul:nth-child(3) {
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 60px;
  }

  li > div {
    width: 60%;
  }

  li.copywright {
    width: 100%;
  }
}

* {
  font-family: "Poppins", sans-serif;
}

p:first-letter {
  text-transform: capitalize;
}

.logo img,
.menu-icon,
a:hover,
button:hover {
  cursor: pointer;
}

.App {
  text-align: center;
}

:root {
  --primary-color: #ed7971;
  --secondary-color: #bd534c;
}

body {
  margin: 0;
}

h1,
h2,
h3 {
  text-transform: capitalize;
}

h1,
h2,
h3,
p {
  color: #333333;
}

button {
  color: #fff;
  padding: 10px;
  border: 0;
  border-radius: 6px;
  text-transform: capitalize;
}

button.primary {
  background-color: #ed7971;
  background-color: var(--primary-color);
  border: 2px solid #ed7971;
  border: 2px solid var(--primary-color);
}

button.secondary {
  background-color: #bd534c;
  background-color: var(--secondary-color);
  border: 2px solid #bd534c;
  border: 2px solid var(--secondary-color);
}

button.outline {
  background-color: white;
  color: #bd534c;
  color: var(--secondary-color);
  border: 2px solid #bd534c;
  border: 2px solid var(--secondary-color);
}

button.disabled {
  background: #CCCCCC;
  border: 1px solid #CCCCCC;
  color: #888888;
}

button.disabled:hover {
  cursor: not-allowed;
}

a.email {
  text-transform: lowercase;
}

section.notfound {
  display: grid;
  grid-template-rows: repeat(2, 0.9fr);
  margin: 30px;
}

aside.notfoundtext {
  grid-row-start: 2;
  text-align: left;
}

aside.notfoundtext h1 {
  text-transform: initial;
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 30px;
}

aside.notfoundtext h1::first-letter {
  text-transform: capitalize;
}

aside.notfoundtext p {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 33px;
}

aside.notfoundimg {
  grid-row-start: 1;
  font-weight: 600;
  font-size: 120px;
  display: grid;
  grid-template-columns: repeat(3, 0.5fr);
  align-items: center;
  justify-items: center;
  width: -webkit-max-content;
  width: max-content;
  margin: auto;
}

@media screen and (min-width: 540px) {
  aside.notfoundtext h1 {
    margin-top: 30px;
  }
}

@media screen and (min-width: 768px) {
  section.notfound {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: none;
    margin: 100px 70px 120px;
    align-items: center;
  }

  aside.notfoundtext {
    grid-column-start: 1;
    grid-row-start: 1;
  }

  aside.notfoundtext h1 {
    margin-top: 0;
  }

  aside.notfoundimg {
    grid-column-start: 2;
  }
}

aside.termsandconditionstext {
  margin: 30px;
  display: grid;
  grid-template-rows: 0.1fr 0.1fr 0.5fr 0.1fr 0.1fr;
  grid-row-gap: 30px;
  row-gap: 30px;
  text-align: left;
}

aside.termsandconditionstext h1 {
  font-weight: 700;
  font-size: 18px;
}

aside.termsandconditionstext p {
  font-size: 16px;
  line-height: 30px;
  margin: 0
}

aside.termsandconditionstext form {
  display: grid;
  grid-template-rows: 50% auto;
  grid-row-gap: 20px;
  row-gap: 20px;
  align-items: center;
}

aside.termsandconditionstext form label {
  display: grid;
  grid-template-columns: 20px auto;
